/* Variáveis */
:root {
  --verde-claro: #35937E;
  --roxo: #774069;
  --rosa: #B82544;
  --roxo-claro: #9B89B3;
}


html {
    font-family: "Montserrat";
  }

  /* ------------ Generics --------------- */

  p {
    font-size: 20px;
  }

  h2 {
    font-size: 36px;
    font-weight: bold;
    text-align: center;
  }

  .logo{
    height: 316px;
  }

  .titulo{
    color: white;
    margin-bottom: 40px;
    margin-top: 40px;
  }

  a {
    color: white;
    text-decoration: none;
  }

  a:hover {
    color: black;
    text-decoration: none;
    font-weight: bold;
  }

  .button {
    width: 400px;
    height: 40px;
    background-color: var(--verde-claro);
    color: white;
    border: none;
    border-radius: 10px;
    box-shadow: 2px 2px 2px rgb(43, 43, 43);
    font-size: large;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .button:hover{
    background-color:#1f7563;
    box-shadow: 4px 4px 8px rgb(43, 43, 43);
    font-weight: bold;
    cursor: pointer;
  }

  .gradiente-container{
    background-image: linear-gradient(var(--roxo), var(--rosa));
    padding: 50px 0px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .roxo-container{
    background-color: var(--roxo-claro);
    padding: 50px 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  /* ------------- Cabeçalho ------------------ */
  
  header {
    background-color: var(--verde-claro);
    margin: 0;
    width: 100%;
    height: 88px;
    /* position: fixed; */
  }

  .center-menu{
    display: flex;
    align-items: center;
    width: 95%;
    height: 100%;
    justify-content: space-between;
    margin: auto auto;
  }

  .logo-foto{
    height: 57px;
  }

  .menu{
    width: 70%;
    margin-right: 10%;
  }
  
  nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

/* -------------- Capa Home ---------------- */

.titulo-capa-home {
  font-size: 36px;
  font-weight: bold;
  text-align: left;
}

.logo-home {
  margin-left: 90px;
}

.capa-home{
  height: 600px;
  width: 100%;
  background-image: url("../public/Capa.png");
  display: flex;
  align-items: center;
}

.capa-home-texto{
  color: white;
  height: 120px;
  padding-right: 100px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}


/* ---------- Footer -------------- */

footer{
  background-color: var(--roxo);
  width: 100%;
  height: 56px;
}

.footer-text-container{
  height: 100%;
}


.footer-text{
  text-align: center;
  font-size: 16px;
  color: white;
  line-height: 56px;
}


/* ----------- home body - habilidades ------------ */

.habilidades-container{
  color: var(--roxo);
}

.habilidades-table {
  margin: 60px auto;
}

tr {
  line-height: 30px;
  font-weight: bold;
}

td {
    border-left: 100px solid transparent;
    border-right: 100px solid transparent;
    background-clip: padding-box;
}

.titulo-habilidades {
  margin-top: 40px;
}

/* -------------- CAPA ---------------------- */

.capa-container{
  background-color: var(--roxo);
  height: 500px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.titulo-capa{
  text-align: center;
  color: white;
  margin-bottom: 30px;
}

.logo-container {
  width: 100%;
  display: flex;
  justify-content: center;
}

/* ----------- Sobre Mim Page ----------------- */

.textao{
  color: white;
  padding: 0px 200px;
}

.textao-line {
  font-size: 18px;
  line-height: 26px;
  margin-bottom: 60px;
}

.textao-link {
  text-decoration: underline;
}

.galeria-subtitulo {
  text-align: center;
  position: relative;
  top: -30px;
  color: white;
}

.fotos-titulo {
  color: white;
  margin-bottom: 30px;
  margin-top: 80px;
}

.legenda{
  color: white;
  margin-top: 8px;
  text-align: center;
}

.music-galery {
  width: 105%;
  display: flex;
  justify-content: center;
  align-items: center;
}

video {
  margin-right: 10px;
}

/* ---------- Trajetoria Body -------------------- */

.exp-data {
  color: white;
  font-weight: bold;
  font-size: 18px;
  text-align: left;
  margin-bottom: 8px;
}

.exp-description {
  color: white;
  text-align: left;
  margin-bottom: 36px;
  line-height: 24px;
}

.conquista-container{
  padding: 0px 200px;
}

.exp-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.entre-outros-container {
  height: 260px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.entre-outros-texto {
  color: var(--roxo-claro);
  width: 50%;
  line-height: 24px;
}

/* --------------- Projetos page ------------------ */

.card-container {
  background-color: var(--roxo-claro);
  width: 70%;
  margin: 20px 0px;
  border-radius: 20px;
}

.card-top-button{
  display: flex;
}

.card-button{
  background: none;
  border: none;
  color: white;
  font-size: 18px;
  margin-left: 8px;
}

.card-button:hover {
  font-size: 22px;
  cursor: pointer;
}

.card-topic {
  display: flex;
  justify-content: space-between;
  padding: 15px;
  width: 100%;
}

.card-nome-projeto {
  color: white;
  font-weight: bold;
  font-size: 18px;
}

.card-tecnologias {
  color: var(--roxo);
  font-size: 13px;
}

.card-content {
  padding: 15px;
  display: flex;
  flex-direction: column;
}

.card-descricao {
  color: var(--roxo);
  font-weight: bold;
  height: 80px;
}

.card-github {
  width: 100%;
  text-align: center;
  color: white;
  font-weight: bold;
}

.card-hidden{
  display: none;
}

/* ------------ Contato Page -------------- */

.icones {
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  position: relative;
  top: -60px;
}

.form-representation {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 60px;
}

input, textarea {
  width: 900px;
  margin: 20px 0px;
  padding: 10px;
  border-radius: 8px;
  border: none;
}

input::placeholder, textarea::placeholder{
  color: var(--rosa);
  font-weight: bold;
}

input:focus, textarea:focus{
  border-style: none;
  border-color: var(--verde-claro);
  box-shadow: 10px 10px var(--roxo-claro);
}

textarea {
  height: 100px;
  resize: none;
}

.contato-agradecimento-container{
  height: 90px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  width: 100%;
}

.agradecimento-text {
  width: 50%;
  text-align: center;
}
